@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Montserrat:wght@400;500;600;700;800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
    -webkit-tap-highlight-color: transparent;
  }
  
  body {
    @apply font-sans text-gray-800 bg-white;
    overflow-x: hidden;
  }
  
  h1, h2, h3, h4, h5, h6 {
    @apply font-heading font-bold;
  }
  
  h1 {
    @apply text-4xl md:text-5xl lg:text-6xl;
  }
  
  h2 {
    @apply text-3xl md:text-4xl;
  }
  
  h3 {
    @apply text-2xl md:text-3xl;
  }
  
  h4 {
    @apply text-xl md:text-2xl;
  }

  /* Custom selection color */
  ::selection {
    @apply bg-primary/30 text-primary-dark;
  }
}

@layer components {
  .container-custom {
    @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
  }
  
  .btn {
    @apply inline-flex items-center justify-center px-6 py-3 rounded-md font-medium transition-all duration-200 transform hover:scale-105 hover:shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2;
  }
  
  .btn-primary {
    @apply btn bg-primary text-white hover:bg-primary-dark focus:ring-primary-dark;
  }
  
  .btn-secondary {
    @apply btn bg-secondary text-white hover:bg-secondary-dark focus:ring-secondary-dark;
  }
  
  .btn-outline {
    @apply btn border-2 bg-transparent hover:bg-opacity-10;
  }
  
  .btn-outline-primary {
    @apply btn-outline border-primary text-primary hover:bg-primary;
  }
  
  .btn-outline-light {
    @apply btn-outline border-white text-white hover:bg-white hover:text-primary;
  }
  
  .section-title {
    @apply text-3xl md:text-4xl font-bold text-center mb-10;
  }
  
  .card-shadow {
    @apply shadow-lg hover:shadow-xl transition-shadow duration-300;
  }
  
  /* Glass effect for cards */
  .glass {
    @apply bg-white bg-opacity-80 backdrop-blur-md border border-white border-opacity-20 shadow-lg;
  }
  
  /* Animated gradient text */
  .gradient-text {
    @apply bg-clip-text text-transparent bg-gradient-to-r from-primary via-secondary to-primary-light bg-size-200 animate-gradient;
  }
  
  /* Animated underline effect */
  .animated-underline {
    @apply relative after:absolute after:bottom-0 after:left-0 after:h-[2px] after:w-0 after:bg-primary after:transition-all after:duration-300 hover:after:w-full;
  }
}

@layer utilities {
  .bg-size-200 {
    background-size: 200% 200%;
  }
  
  .animate-gradient {
    animation: gradientShift 3s ease infinite;
  }
  
  @keyframes gradientShift {
    0%, 100% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
  }
  
  /* Responsive spacing */
  .section-spacing {
    @apply py-12 md:py-16 lg:py-20;
  }
  
  /* Background patterns */
  .bg-dots {
    background-image: radial-gradient(circle, currentColor 1px, transparent 1px);
    background-size: 16px 16px;
  }
  
  .bg-grid {
    background-image: linear-gradient(to right, rgba(0,0,0,0.05) 1px, transparent 1px),
                      linear-gradient(to bottom, rgba(0,0,0,0.05) 1px, transparent 1px);
    background-size: 24px 24px;
  }
}
